import {RU} from './RU';
import {EN} from './EN';
import {SITE_LANG} from "../tools/constants";




export function getText(word){
    if (localStorage.getItem(SITE_LANG) === "ru"){
        return RU[word];
    } else return EN[word];
}

export function getLang(){
    return localStorage.getItem(SITE_LANG);
}